"use client";

import { useEffect, useMemo, useState } from "react";
import type React from "react";
import Swal from "sweetalert2";
import DataTable from "./DataTable";
import { adminMaterialsApi, type MaterialRecord, type MaterialsListResponse } from "@/lib/api/admin";

type ResourceType = "materi" | "kuis";

export interface ModuleItemRow {
  id: string;
  title: string;
  description?: string;
  content?: string; // materi: full content for editing
  updated_at?: string;
  published?: boolean;
  // Optional fields per resource type
  contentLength?: number; // materi
  questionCount?: number; // kuis
}

interface ModuleItemsPageProps {
  moduleId: string;
  resource: ResourceType;
}

const STORAGE_PREFIX = "admin_module_resource_";

export default function ModuleItemsPage({ moduleId, resource }: ModuleItemsPageProps) {
  const title = resource === "materi" ? "Materi" : "Kuis";
  const searchPlaceholder = resource === "materi" ? "Cari materi..." : "Cari kuis...";
  const storageKey = `${STORAGE_PREFIX}${resource}_${moduleId}`;

  const [rows, setRows] = useState<ModuleItemRow[]>([]);
  // Add form state (no SweetAlert for add)
  const [showAdd, setShowAdd] = useState(false);
  const [aTitle, setATitle] = useState("");
  const [aPublished, setAPublished] = useState(false);
  const [aSlug, setASlug] = useState(""); // materi optional
  const [aContent, setAContent] = useState(""); // materi
  const [aDescription, setADescription] = useState(""); // kuis placeholder
  const [aQuestionCount, setAQuestionCount] = useState<string>(""); // kuis placeholder
  const [aSubmitting, setASubmitting] = useState(false);

  // Edit form state (inline form instead of SweetAlert)
  const [editing, setEditing] = useState<ModuleItemRow | null>(null);
  const [eTitle, setETitle] = useState("");
  const [ePublished, setEPublished] = useState(false);
  const [eSlug, setESlug] = useState(""); // materi optional
  const [eContent, setEContent] = useState(""); // materi: leave empty to keep unchanged
  const [eDescription, setEDescription] = useState(""); // kuis placeholder
  const [eQuestionCount, setEQuestionCount] = useState<string>(""); // kuis placeholder
  const [eSubmitting, setESubmitting] = useState(false);

  // Helper to normalize list response to array
  const toMaterialArray = (payload: unknown): MaterialRecord[] => {
    if (Array.isArray(payload)) return payload as MaterialRecord[];
    const obj = payload as MaterialsListResponse;
    if (obj && Array.isArray(obj.items)) return obj.items as MaterialRecord[];
    if (obj && Array.isArray(obj.data)) return obj.data as MaterialRecord[];
    return [];
  };

  // Helper to format server error from our apiFetch union result
  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  const formatServerError = (res: { ok: false; status: number; error: string; raw?: any }) => {
    const j = res.raw && typeof res.raw === 'object' ? (res.raw as Record<string, unknown>) : undefined;
    const code = typeof j?.code === 'string' ? j?.code : undefined;
    const message = (typeof j?.message === 'string' && j?.message) || res.error;
    const details = typeof j?.details === 'string' ? j?.details : undefined;
    const head = code ? `[${code}] ${message}` : message;
    return details ? `${head}\nDetail: ${details}` : head;
  };

  // Load data from backend for materi; keep local seed for kuis (placeholder)
  useEffect(() => {
    const load = async () => {
      if (resource === "materi") {
        const res = await adminMaterialsApi.list({ module_id: moduleId });
        if (res.ok) {
          const arr: MaterialRecord[] = toMaterialArray(res.data as unknown);
          const mapped: ModuleItemRow[] = arr.map((m) => ({
            id: String(m.id),
            title: m.title,
            description: typeof m.content === 'string' ? m.content.slice(0, 140) : undefined,
            content: typeof m.content === 'string' ? m.content : undefined,
            updated_at: m.updated_at || m.created_at,
            published: !!m.published,
            contentLength: typeof m.content === 'string' ? m.content.length : 0,
          }));
          setRows(mapped);
          try { localStorage.setItem(storageKey, JSON.stringify(mapped)); } catch { }
          return;
        }
      }
    };
    load();
  }, [moduleId, resource, storageKey]);

  // Persist to localStorage on change
  useEffect(() => {
    try {
      localStorage.setItem(storageKey, JSON.stringify(rows));
    } catch {
      // ignore
    }
  }, [rows, storageKey]);

  // eslint-disable-next-line @typescript-eslint/no-explicit-any
  type ColumnDef = { key: string; label: string; render?: (value: any, row: ModuleItemRow) => React.ReactNode };
  const columns: ColumnDef[] = useMemo(() => {
    return [
      {
        key: "title",
        label: title,
        render: (_: unknown, row: ModuleItemRow) => (
          <div>
            <div className="font-medium text-gray-900">{row.title}</div>
            {row.description && (
              <div className="text-sm text-gray-500 truncate max-w-[360px]">{row.description}</div>
            )}
          </div>
        )
      },
      {
        key: resource === "materi" ? "contentLength" : "questionCount",
        label: resource === "materi" ? "Panjang Konten" : "Jumlah Soal",
        render: (val: number | undefined) => (
          <span className="text-sm text-gray-700">{val ?? 0} {resource === "materi" ? "kata" : "soal"}</span>
        )
      },
      {
        key: "published",
        label: "Status",
        render: (v: boolean | undefined) => (
          <span className={`inline-flex items-center px-2 py-0.5 rounded-full text-xs font-medium ${v ? "bg-green-100 text-green-700" : "bg-gray-200 text-gray-700"}`}>
            {v ? "Terbit" : "Draf"}
          </span>
        )
      },
      {
        key: "updated_at",
        label: "Diubah",
        render: (val?: string) => (
          <span className="text-sm text-gray-700">{val ? new Date(val).toLocaleString("id-ID") : "-"}</span>
        )
      }
    ];
  }, [resource, title]);

  const onAdd = () => setShowAdd((v) => !v);

  const submitAdd = async (e: React.FormEvent) => {
    e.preventDefault();
    const titleVal = aTitle.trim();
    if (!titleVal || titleVal.length < 3) {
      await Swal.fire({ icon: 'warning', title: 'Judul minimal 3 karakter' });
      return;
    }

    if (resource === 'materi') {
      const contentVal = aContent.trim();
      if (!contentVal || contentVal.length < 10) {
        await Swal.fire({ icon: 'warning', title: 'Konten minimal 10 karakter' });
        return;
      }
      setASubmitting(true);
      const res = await adminMaterialsApi.createNested(moduleId, {
        title: titleVal,
        content: contentVal,
        published: aPublished,
        slug: aSlug ? aSlug.trim() : undefined,
      });
      setASubmitting(false);
      if (!res.ok) {
        await Swal.fire({ icon: 'error', title: 'Gagal', text: formatServerError(res as unknown as { ok: false; status: number; error: string; raw?: unknown }) || 'Gagal membuat materi' });
        return;
      }
      const list = await adminMaterialsApi.list({ module_id: moduleId });
      if (list.ok) {
        const arr: MaterialRecord[] = toMaterialArray(list.data as unknown);
        const mapped: ModuleItemRow[] = arr.map((m) => ({
          id: String(m.id),
          title: m.title,
          description: typeof m.content === 'string' ? m.content.slice(0, 140) : undefined,
          content: typeof m.content === 'string' ? m.content : undefined,
          updated_at: m.updated_at || m.created_at,
          published: !!m.published,
          contentLength: typeof m.content === 'string' ? m.content.length : 0,
        }));
        setRows(mapped);
        try { localStorage.setItem(storageKey, JSON.stringify(mapped)); } catch { }
      }
      await Swal.fire({ icon: 'success', title: 'Berhasil', timer: 900, showConfirmButton: false });
      // reset & close
      setATitle(""); setAContent(""); setASlug(""); setAPublished(false); setShowAdd(false);
      return;
    }

    // Kuis placeholder (local only until API is available)
    const now = new Date().toISOString();
    const qCount = aQuestionCount ? Number(aQuestionCount) : 0;
    const newItem: ModuleItemRow = { id: crypto.randomUUID(), title: titleVal, description: aDescription || '', published: aPublished, updated_at: now, questionCount: Number.isFinite(qCount) ? qCount : 0 };
    const next = [newItem, ...rows];
    setRows(next);
    try { localStorage.setItem(storageKey, JSON.stringify(next)); } catch { }
    await Swal.fire({ icon: 'success', title: 'Tersimpan', timer: 900, showConfirmButton: false });
    setATitle(""); setADescription(""); setAPublished(false); setAQuestionCount(""); setShowAdd(false);
  };

  // Begin edit: open inline form with row values
  const onEdit = (row: ModuleItemRow) => {
    setShowAdd(false);
    setEditing(row);
    setETitle(row.title || "");
    setEPublished(!!row.published);
    setESlug("");
    setEContent(row.content || ""); // prefill with existing content if available
    setEDescription(row.description || "");
    setEQuestionCount(typeof row.questionCount === 'number' ? String(row.questionCount) : "");
  };

  const cancelEdit = () => {
    setEditing(null);
    setETitle("");
    setEPublished(false);
    setESlug("");
    setEContent("");
    setEDescription("");
    setEQuestionCount("");
    setESubmitting(false);
  };

  const submitEdit = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!editing) return;
    const titleVal = eTitle.trim();
    if (!titleVal || titleVal.length < 3) {
      await Swal.fire({ icon: 'warning', title: 'Judul minimal 3 karakter' });
      return;
    }

    if (resource === 'materi') {
      const contentVal = eContent.trim();
      if (contentVal && contentVal.length < 10) {
        await Swal.fire({ icon: 'warning', title: 'Konten minimal 10 karakter atau kosongkan untuk tidak mengubah' });
        return;
      }
      setESubmitting(true);
      const payload: { title?: string; content?: string; published?: boolean; slug?: string } = {};
      // always send title and published; content/slug only when provided
      payload.title = titleVal;
      payload.published = !!ePublished;
      if (contentVal) payload.content = contentVal;
      if (eSlug.trim()) payload.slug = eSlug.trim();
      const res = await adminMaterialsApi.update(editing.id, payload);
      setESubmitting(false);
      if (!res.ok) {
        await Swal.fire({ icon: 'error', title: 'Gagal', text: formatServerError(res as unknown as { ok: false; status: number; error: string; raw?: unknown }) || 'Gagal memperbarui materi' });
        return;
      }
      // reload list
      const list = await adminMaterialsApi.list({ module_id: moduleId });
      if (list.ok) {
        const arr: MaterialRecord[] = toMaterialArray(list.data as unknown);
        const mapped: ModuleItemRow[] = arr.map((m) => ({
          id: String(m.id),
          title: m.title,
          description: typeof m.content === 'string' ? m.content.slice(0, 140) : undefined,
          content: typeof m.content === 'string' ? m.content : undefined,
          updated_at: m.updated_at || m.created_at,
          published: !!m.published,
          contentLength: typeof m.content === 'string' ? m.content.length : 0,
        }));
        setRows(mapped);
        try { localStorage.setItem(storageKey, JSON.stringify(mapped)); } catch { }
      }
      await Swal.fire({ icon: 'success', title: 'Tersimpan', timer: 900, showConfirmButton: false });
      cancelEdit();
      return;
    }

    // kuis placeholder: local update
    const qCount = eQuestionCount ? Number(eQuestionCount) : 0;
    setRows((prev) => prev.map((r) => r.id === editing.id ? { ...r, title: titleVal, description: eDescription, published: ePublished, questionCount: Number.isFinite(qCount) ? qCount : 0, updated_at: new Date().toISOString() } : r));
    await Swal.fire({ icon: 'success', title: 'Tersimpan', timer: 900, showConfirmButton: false });
    cancelEdit();
  };

  const onDelete = async (row: ModuleItemRow) => {
    const result = await Swal.fire({
      title: `Hapus ${title}?`,
      text: `Item \"${row.title}\" akan dihapus.`,
      icon: 'warning',
      showCancelButton: true,
      confirmButtonText: 'Hapus',
      cancelButtonText: 'Batal',
      reverseButtons: true
    });
    if (!result.isConfirmed) return;
    if (resource === 'materi') {
      const res = await adminMaterialsApi.remove(row.id);
      if (!res.ok) {
        await Swal.fire({ icon: 'error', title: 'Gagal', text: formatServerError(res as unknown as { ok: false; status: number; error: string; raw?: unknown }) || 'Gagal menghapus materi' });
        return;
      }
    }
    setRows((prev) => prev.filter((r) => r.id !== row.id));
  };

  return (
    <div className="p-6 bg-gray-50 min-h-screen text-black">
      {/* Edit form section */}
      {editing && (
        <form onSubmit={submitEdit} className="bg-white rounded-xl p-5 shadow-sm border border-gray-200 mb-6">
          <h2 className="text-lg font-semibold text-gray-900 mb-4">Ubah {title}</h2>
          {resource === 'materi' ? (
            <div className="grid grid-cols-1 gap-4">
              <div>
                <label className="text-sm text-gray-600">Judul *</label>
                <input value={eTitle} onChange={(ev) => setETitle(ev.target.value)} className="w-full px-3 py-2 border text-black border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500" placeholder="Judul minimal 3 karakter" required minLength={3} />
              </div>
              <div>
                <label className="text-sm text-gray-600">Konten</label>
                <textarea value={eContent} onChange={(ev) => setEContent(ev.target.value)} className="w-full px-3 py-2 border text-black border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 min-h-32" placeholder="Perbarui konten di sini (minimal 10 karakter)" />
              </div>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <label className="text-sm text-gray-600">Slug (opsional)</label>
                  <input value={eSlug} onChange={(ev) => setESlug(ev.target.value)} className="w-full px-3 py-2 border text-black border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500" placeholder="biarkan kosong untuk tidak mengubah" />
                </div>
                <div className="flex items-center gap-2 pt-6">
                  <input id="e-published" type="checkbox" checked={ePublished} onChange={(ev) => setEPublished(ev.target.checked)} className="w-4 h-4" />
                  <label htmlFor="e-published" className="text-sm text-gray-700">Terbitkan</label>
                </div>
              </div>
              <div>
                <button type="submit" disabled={eSubmitting} className="inline-flex items-center gap-2 px-4 py-2 rounded-lg bg-blue-600 hover:bg-blue-700 text-white shadow disabled:opacity-60">
                  {eSubmitting ? 'Menyimpan...' : 'Simpan Perubahan'}
                </button>
                <button type="button" onClick={cancelEdit} className="ml-2 inline-flex items-center gap-2 px-4 py-2 rounded-lg bg-gray-100 hover:bg-gray-200 text-gray-700 shadow">
                  Batal
                </button>
              </div>
            </div>
          ) : (
            <div className="grid grid-cols-1 gap-4">
              <div>
                <label className="text-sm text-gray-600">Judul *</label>
                <input value={eTitle} onChange={(ev) => setETitle(ev.target.value)} className="w-full px-3 py-2 border text-black border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500" placeholder="Judul minimal 3 karakter" required minLength={3} />
              </div>
              <div>
                <label className="text-sm text-gray-600">Deskripsi</label>
                <input value={eDescription} onChange={(ev) => setEDescription(ev.target.value)} className="w-full px-3 py-2 border text-black border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500" placeholder="Opsional" />
              </div>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <label className="text-sm text-gray-600">Jumlah Soal</label>
                  <input type="number" min={0} value={eQuestionCount} onChange={(ev) => setEQuestionCount(ev.target.value)} className="w-full px-3 py-2 border text-black border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500" placeholder="mis. 10" />
                </div>
                <div className="flex items-center gap-2 pt-6">
                  <input id="eq-published" type="checkbox" checked={ePublished} onChange={(ev) => setEPublished(ev.target.checked)} className="w-4 h-4" />
                  <label htmlFor="eq-published" className="text-sm text-gray-700">Terbitkan</label>
                </div>
              </div>
              <div>
                <button type="submit" className="inline-flex items-center gap-2 px-4 py-2 rounded-lg bg-blue-600 hover:bg-blue-700 text-white shadow">
                  Simpan Perubahan
                </button>
                <button type="button" onClick={cancelEdit} className="ml-2 inline-flex items-center gap-2 px-4 py-2 rounded-lg bg-gray-100 hover:bg-gray-200 text-gray-700 shadow">
                  Batal
                </button>
              </div>
            </div>
          )}
        </form>
      )}
      {/* Add form section */}
      {showAdd && (
        <form onSubmit={submitAdd} className="bg-white rounded-xl p-5 shadow-sm border border-gray-200 mb-6">
          <h2 className="text-lg font-semibold text-gray-900 mb-4">Tambah {title} Baru</h2>
          {resource === 'materi' ? (
            <div className="grid grid-cols-1 gap-4">
              <div>
                <label className="text-sm text-gray-600">Judul *</label>
                <input value={aTitle} onChange={(e) => setATitle(e.target.value)} className="w-full px-3 py-2 border text-black border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500" placeholder="Judul minimal 3 karakter" required minLength={3} />
              </div>
              <div>
                <label className="text-sm text-gray-600">Konten *</label>
                <textarea value={aContent} onChange={(e) => setAContent(e.target.value)} className="w-full px-3 py-2 border text-black border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 min-h-32" placeholder="Konten minimal 10 karakter" required />
              </div>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <label className="text-sm text-gray-600">Slug (opsional)</label>
                  <input value={aSlug} onChange={(e) => setASlug(e.target.value)} className="w-full px-3 py-2 border text-black border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500" placeholder="biarkan kosong untuk auto" />
                </div>
                <div className="flex items-center gap-2 pt-6">
                  <input id="a-published" type="checkbox" checked={aPublished} onChange={(e) => setAPublished(e.target.checked)} className="w-4 h-4" />
                  <label htmlFor="a-published" className="text-sm text-gray-700">Terbitkan</label>
                </div>
              </div>
              <div>
                <button type="submit" disabled={aSubmitting} className="inline-flex items-center gap-2 px-4 py-2 rounded-lg bg-blue-600 hover:bg-blue-700 text-white shadow disabled:opacity-60">
                  {aSubmitting ? 'Menyimpan...' : 'Simpan'}
                </button>
                <button type="button" onClick={() => setShowAdd(false)} className="ml-2 inline-flex items-center gap-2 px-4 py-2 rounded-lg bg-gray-100 hover:bg-gray-200 text-gray-700 shadow">
                  Batal
                </button>
              </div>
            </div>
          ) : (
            <div className="grid grid-cols-1 gap-4">
              <div>
                <label className="text-sm text-gray-600">Judul *</label>
                <input value={aTitle} onChange={(e) => setATitle(e.target.value)} className="w-full px-3 py-2 border text-black border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500" placeholder="Judul minimal 3 karakter" required minLength={3} />
              </div>
              <div>
                <label className="text-sm text-gray-600">Deskripsi</label>
                <input value={aDescription} onChange={(e) => setADescription(e.target.value)} className="w-full px-3 py-2 border text-black border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500" placeholder="Opsional" />
              </div>
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <label className="text-sm text-gray-600">Jumlah Soal</label>
                  <input type="number" min={0} value={aQuestionCount} onChange={(e) => setAQuestionCount(e.target.value)} className="w-full px-3 py-2 border text-black border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500" placeholder="mis. 10" />
                </div>
                <div className="flex items-center gap-2 pt-6">
                  <input id="q-published" type="checkbox" checked={aPublished} onChange={(e) => setAPublished(e.target.checked)} className="w-4 h-4" />
                  <label htmlFor="q-published" className="text-sm text-gray-700">Terbitkan</label>
                </div>
              </div>
              <div>
                <button type="submit" className="inline-flex items-center gap-2 px-4 py-2 rounded-lg bg-blue-600 hover:bg-blue-700 text-white shadow">
                  Simpan
                </button>
                <button type="button" onClick={() => setShowAdd(false)} className="ml-2 inline-flex items-center gap-2 px-4 py-2 rounded-lg bg-gray-100 hover:bg-gray-200 text-gray-700 shadow">
                  Batal
                </button>
              </div>
            </div>
          )}
        </form>
      )}

      <DataTable<ModuleItemRow>
        title={`${title} Modul`}
        data={rows}
        columns={columns}
        onAdd={onAdd}
        onEdit={onEdit}
        onDelete={onDelete}
        searchPlaceholder={searchPlaceholder}
      />
    </div>
  );
}
